#root {
  background-color: white;
}

div.card {
  background-color: rgb(245, 245, 245);
  border-color: transparent;
  border-radius: 20px;
  padding: 20px;
  /* box-shadow: 0px 0px 5px rgba(192, 192, 192, 0.342); */
}

