button {
  background-color: #e6e6e6;
  border-radius: 10px;
  border-color: transparent;
  color: white;
  padding: 0px 10px;
  color: #04aa6d;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 10px;
}

